.championSkins {
    display: none;
    margin: 0 auto;
    width: 100%;
    top: 0;
    left: 0;
    margin-top: 50px;
    z-index: 3;
    padding-bottom: 25px;

    &--active {
        display: block;
    }

    &__wrapper {
        margin: 0 auto;
        max-width: 1100px;
        display: flex;
        justify-content: space-around;
        position: relative;
        color: white;
        min-height: 400px;
        border-top: 2px ridge #00bbd5;
        background-color: rgb(0 0 0 / 73%);
        flex-wrap: wrap;


        @media(max-width: 775px) {
            flex-wrap: wrap;
        }
    }

    &__title {
        padding: 25px 0;
        width: 100%;
        text-align: center;
    }

    &__list {
        list-style: none;
        width: 80%;
        display: flex;
        flex-wrap: wrap;
        margin: 0 auto;
        list-style: none;
        padding: 0;
        margin-bottom: 35px;
        justify-content: space-between;

        @media(max-width: 1020px) {
            justify-content: center;
        }
    }

    &__image {
        max-width: 400px;
    }

    &__skinName {
        text-align: center;
        padding: 10px 0;
        width: 100%;
    }

    &__bioToolTip {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px 15px;
        color: white;
        background-color: black;
        border: 1px solid rgb(111, 67, 1);
        flex-wrap: wrap;
    }

    &__bioTitle {
        width: 100%;
        display: block;
        text-align: center;
        padding: 10px 0;
    }

    &__bioDecor {
        width: 80%;
        display: block;
        align-self: center;
        height: 1px;
        // background-color: #6f4301;
        margin: 3px 0px 5px 0px;
        background: hsla(47, 61%, 59%, 1);

        background: linear-gradient(90deg, hsla(47, 61%, 59%, 0.4) 0%, hsla(36, 98%, 22%, 0.9) 40%, hsla(36, 98%, 22%, 0.9) 60%, hsla(47, 61%, 59%, 0.4) 100%);

        background: -moz-linear-gradient(90deg, hsla(47, 61%, 59%, 0.4) 0%, hsla(36, 98%, 22%, 0.9) 40%, hsla(36, 98%, 22%, 0.9) 60%, hsla(47, 61%, 59%, 0.4) 100%);

        background: -webkit-linear-gradient(90deg, hsla(47, 61%, 59%, 0.4) 0%, hsla(36, 98%, 22%, 0.9) 40%, hsla(36, 98%, 22%, 0.9) 60%, hsla(47, 61%, 59%, 0.4) 100%);

    }

}