.championInfo {
    display: none;
    margin: 0 auto;
    width: 100%;
    top: 0;
    left: 0;
    margin-top: 50px;
    z-index: 3;

    &--active {
        display: block;
    }


    &__wrapper {
        margin: 0 auto;
        max-width: 1100px;
        display: flex;
        justify-content: space-around;
        position: relative;
        color: white;
        min-height: 400px;
        border-bottom: 2px ridge #00bbd5;
        background-color: rgb(0 0 0 / 73%);


        @media(max-width: 775px) {
            flex-wrap: wrap;
        }
    }

    &__sector {
        margin: 0;

        &--left {
            min-width: 200px;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &--right {
            padding-top: 40px;
            min-width: 230px;
            padding-left: 20px;
        }
    }

    &__title {
        width: 100%;
        text-align: center;
    }

    &__skillsList {
        list-style: none;
        margin: 0 auto;
        padding: 0;
        display: flex;
        justify-content: space-around;
        max-width: 400px;
    }

    &__stats {
        display: flex;
        align-items: center;
        margin-bottom: 5px;
    }

    &__statsTitle {
        font-size: 25px;
        margin-bottom: 15px;
    }

    &__statsName {
        min-width: 70px;
        display: flex;
        justify-content: flex-end;
        margin-right: 5px;
    }

    &__statsPoint {
        background-color: rgba(39, 114, 155, 0.789);
        border-radius: 5px;
        width: 10px;
        height: 20px;
        margin-left: 5px;
    }

    &__lore {
        @media (max-width: 785px) {
            padding: 15px;
        }
    }

    &__closeBtn {
        right: 4%;
        top: 1%;
        position: absolute;
        font-size: 30px;
        user-select: none;
        transition: all 0.3s ease-in-out;

        &:hover {
            cursor: pointer;
            color: rgb(229, 255, 0);
            transition: all 0.3s ease-in-out;
            transform: scale(1.2);
        }
    }

    &__tooltip {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px 15px;
        color: white;
        background-color: black;
        border: 1px solid rgb(111, 67, 1);
    }

    &__tooltip-desc {
        display: block;
        padding: 10px;
    }

    &__status {
        color: rgb(145, 11, 255);
    }

    &__phyDmg {
        color: orange;
    }

    &__mDmg {
        color: rgb(0, 145, 255);
    }

    &__scaleAD {
        color: rgb(235, 94, 94);
    }

    &__scaleAP {
        color: rgb(0, 145, 255);
    }

    &__healing {
        color: rgb(22, 216, 77);
    }

    &__trueDmg {
        color: rgb(255, 255, 255);
    }

    &__spd {
        color: rgb(122, 197, 255);
    }

    &__passive {
        color: rgb(122, 197, 255);
    }

    &__active {
        color: rgb(255, 247, 0);
    }

    &__recast {
        color: rgb(166, 245, 255);
    }

    &__br {
        display: inline-block;
        width: 100%;
    }
}

