.champions {
    display: flex;
    &--hide {
        display: none;
    }
    &__list {
        display: flex;
        flex-wrap: wrap;
        margin: 0 auto;
        list-style: none;
        padding: 0;
        max-width: 85%;
    }

    &__item {
        filter: brightness(0.5);
        margin: 6px;
        padding: 0;
        width: 65px;
        height: 65px;
        transition: transform 0.2s ease-in;
        position: relative;

        &:hover,
        &:focus-within {
            z-index: 2;
            outline: none;
            outline: 1px solid rgb(103, 51, 223);
            cursor: pointer;
            filter: brightness(1.1);
            transform: scale(1.2);
        }

        &:hover .champions__tooltip,
        &:focus-within .champions__tooltip {
            display: flex;
            position: absolute;
        }
    }

    &__tooltip {
        display: none;
        color: white;
        position: absolute;
        font-size: 10px;
        border: 1px solid #c3c3c3;
        border-radius: 5%;
        background-color: rgb(43 18 112);
        width: 65px;
        justify-content: center;
        top: 80%;
        left: 0;
    }

    &__image {
        width: 65px;
        height: 65px;
    }



}