.App {
    background-image: url("../../img/wallpaper.jpg");
    background-size: cover;
    background-attachment: fixed;
    min-height: 1083px;
    position: relative;


    &__champBG {
        width: 100%;
        height: 100%;
        opacity: 0;
        position: absolute;
        background-size: 100% 100%;
        background-attachment: fixed;
        background-repeat: no-repeat;
        // min-height: 1083px;
        // padding-bottom: 50px;
        transition: all 0.2s ease-in-out;
    }

    &__header {
        position: relative;
    }

    &__topPanel {
        display: flex;
        justify-content: center;
    }
}