.loadingKata {
    margin: 0 auto;
    align-self: center;

    &__wrapper {
        display: flex;
    }

    &__title {
        padding-left: 1px;
        visibility: visible;

        &::after {
            content: ' .';
            animation: dot 1s steps(5, end) infinite;
        }
    }

    @keyframes dot {

        0%,
        20% {
            color: rgba(0, 0, 0, 0);
            text-shadow:
                .25em 0 0 rgba(0, 0, 0, 0),
                .5em 0 0 rgba(0, 0, 0, 0);
        }

        40% {
            color: white;
            text-shadow:
                .25em 0 0 rgba(0, 0, 0, 0),
                .5em 0 0 rgba(0, 0, 0, 0);
        }

        60% {
            text-shadow:
                .25em 0 0 white,
                .5em 0 0 rgba(0, 0, 0, 0);
        }

        80%,
        100% {
            text-shadow:
                .25em 0 0 white,
                .5em 0 0 white;
        }
    }

}