.championSearch {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 50px;
    z-index: 152;
    margin: 0 auto;
    -webkit-box-shadow: 0px 16px 7px 0px rgba(0, 144, 255, 0.2);
    -moz-box-shadow: 0px 16px 7px 0px rgba(0, 144, 255, 0.2);
    box-shadow: 0px 16px 7px 0px rgba(0, 144, 255, 0.2);

    @media (min-width: 650px) {
        width: 650px;
    }

    &__input {
        color: white;
        background-color: transparent;
        border: none;
        min-width: 320px;
        min-height: 40px;
        text-align: center;
        outline: none;

        &::placeholder {
            color: white;
        }
    }
}